\n\n\t\ta > &,\n\t\th1 > &, h2 > &, h3 > &, h4 > &, h5 > &, h6 > &,\n\t\t.h1 > &, .h2 > &, .h3 > &, .h4 > &, .h5 > &, .h6 > & {\n\t\t\tfont-size: inherit;\n\t\t}\n\t}\n\n\ta > code {\n\t\tcolor: $body-color;\n\t}\n\n\t:not(pre)>code.hljs {\n\t\tbackground: mix($black, $tab-base-bg, 13%); // = #4d4748\n\t}\n\n\tpre code,\n\tpre code.hljs {\n\t\tbackground: mix($black, $tab-base-bg, 13%); // = #4d4748\n\t\tborder-radius: calculateRem(5px);\n\t\tdisplay: block;\n\t\tpadding: .5em .75em;\n\t\toverflow-x: auto;\n\t}\n\n\tsection,\n\tfooter {\n\t\tpadding-top: map-get($spacers, 5);\n\t\tpadding-bottom:  map-get($spacers, 5);\n\n\t\t&#intro {\n\t\t\tpadding-top: $navbar-height + map-get($spacers, 5);\n\t\t}\n\n\t\t&#features {\n\t\t\tpadding-bottom: 0;\n\t\t}\n\n\t\t&.docs-content {\n\t\t\tpadding-top: 0;\n\t\t}\n\n\t\t@include media-breakpoint-down(md) {\n\t\t\tpadding-top: map-get($spacers, 5) / 2;\n\t\t\tpadding-bottom:  map-get($spacers, 5) / 2;\n\n\t\t\t&#intro {\n\t\t\t\tpadding-top: $navbar-height / 1.1 + map-get($spacers, 5) / 2;\n\t\t\t}\n\t\t}\n\n\t\t@include media-breakpoint-down(sm) {\n\t\t\t&#features,\n\t\t\t&.docs-content {\n\t\t\t\tpadding-bottom: (map-get($spacers, 5) / 2) - $spacer;\n\t\t\t}\n\t\t}\n\t}\n\n// MODAL\n.modal {\n\toverflow: hidden;\n}\n\n.modal-full {\n\tmax-width: 100%;\n\n\t.close {\n\t\tposition: absolute;\n\t\twidth: 2rem;  \t\t\t\t\t\t// 1.5625rem;\n\t\theight: 2rem;  \t\t\t\t\t\t// 1.5625rem;\n\t\tright: 1.5rem;\n\t\ttop: 1.5rem;\n\t\tleft: auto;\n\t\topacity: 1 !important;\n\n\t\t&:before,\n\t\t&:after {\n\t\t\tcontent: '';\n\t\t\tposition: absolute;\n\t\t\tleft: 0%;\n\t\t\ttop: 50%;\n\t\t\ttransform: translate(-50%, -50%);\n\t\t\twidth: 100%;\n\t\t\theight: .125rem;\n\t\t\tbackground: $white;\n\t\t}\n\n\t\t&:before {\n\t\t\ttransform: rotate(45deg);\n\t\t}\n\n\t\t&:after {\n\t\t\ttransform: rotate(-45deg);\n\t\t}\n\t}\n\n\t&.modal-dialog {\n\t\tposition: fixed;\n\t\tmargin: 0;\n\t\twidth: 100%;\n\t\theight: 100%;\n\t\tpadding: 0;\n\t}\n\n\t.modal-content {\n\t\tposition: absolute;\n\t\ttop: 0;\n\t\tright: 0;\n\t\tbottom: 0;\n\t\tleft: 0;\n\t\tborder-radius: 0;\n\t\tbox-shadow: none;\n\t}\n\n\t.modal-body {\n\t\tposition: absolute;\n\t\tdisplay: flex;\n\t\talign-items: center;\n\t\tjustify-content: center;\n\t\ttop: 4rem;\n\t\tbottom: 4rem;\n\t\tleft: 4rem;\n\t\tright: 4rem;\n\n\t\t@include media-breakpoint-down(xs) {\n\t\t\tleft: 2rem;\n\t\t\tright: 2rem;\n\t\t}\n\n\t\t@media screen and (max-height: 300px) {\n\t\t\tleft: 4rem;\n\t\t\tright: 4rem;\n\t\t}\n\t}\n}\n\n\n// Nav\n\t.navbar {\n\t\tposition: absolute;\n\t\ttop: 0;\n\t\tleft: 0;  // ie...\n\t\twidth: 100%;\n\t\tz-index: $zindex-fixed;\n\t}\n\n\t.navbar-brand {\n\t\tpadding-top: $navbar-brand-padding-t;\n\t\tpadding-bottom: $navbar-brand-padding-b;\n\n\t\t@include media-breakpoint-down(sm) {\n\t\t\tpadding-left: $grid-gutter-width / 2;\n\t\t}\n\n\t\t@include media-breakpoint-down(xs) {\n\t\t\tpadding-left: 1.2rem;\n\t\t}\n\t}\n\n\t.navbar-light {\n\t\tbackground: $navbar-light-bg;\n\t}\n\n\t.navbar-toggler {\n\t\t@extend .mr-2_2;\n\t\t@extend .mr-md-0;\n\t}\n\n\t.navbar-collapse {\n\t\t@extend .justify-content-md-end;\n\n\t\t.navbar-right {\n\t\t\t@extend .d-flex;\n\t\t\t@extend .flex-column;\n\t\t\t@extend .align-items-end;\n\t\t\t@extend .flex-md-row;\n\t\t\t@extend .text-right;\n\n\t\t\t@extend .mx-2_3;\n\t\t\t@extend .mx-md-0;\n\t\t}\n\t}\n\n\t.navbar-collapse-shown {\n\t\toverflow: hidden;\n\n\t\t.navbar-overlay {\n\t\t\topacity: 1;\n\t\t\tvisibility: visible;\n\t\t}\n\n\t\tsection > .container,\n\t\t.navbar ~ .container {\n\t\t\tfilter: blur(calculateRem(3px));\n\t\t}\n\n\t\t@at-root {\n\t\t\t.navbar-overlay {\n\t\t\t\t@include addAnimation(opacity, .1s);\n\t\t\t\topacity: 0;\n\t\t\t\tposition: fixed;\n\t\t\t\tz-index: $zindex-fixed - 1;\n\t\t\t\ttop: 0; right: 0; bottom: 0; left: 0;\n\t\t\t\tbackground:  rgba($white, .8);\n\t\t\t\tvisibility: hidden;\n\t\t\t}\n\n\t\t\tsection > .container {\n\t\t\t\t@include addAnimation(filter, .1s);\n\t\t\t\tfilter: blur(0);\n\t\t\t}\n\t\t}\n\t}\n\n\t@include media-breakpoint-only(md) {\n\t\t.navbar-expand-md .navbar-nav .nav-item .nav-link {\n\t\t\tpadding-left: $navbar-nav-link-padding-x / 2;\n\t\t\tpadding-right: $navbar-nav-link-padding-x / 2;\n\t\t}\n\t}\n\n\t#navbar-social-links {\n\t\t@extend .flex-row;\n\t\t@extend .ml-md-navbar-nav-link-px;\n\t\t@extend .my-2_2;\n\t\t@extend .my-md-0;\n\n\t\t.nav-item {\n\n\t\t\t&:not(:first-child) .nav-link {\n\t\t\t\t@extend .pr-0;\n\t\t\t}\n\n\t\t\t&:not(:last-child) .nav-link {\n\t\t\t\t@extend .pl-0;\n\t\t\t}\n\n\t\t\t.nav-link {\n\t\t\t\tpadding-left: .625rem;\n\t\t\t\tpadding-right: .625rem;\n\t\t\t\topacity: .98;\n\n\t\t\t\t@include hover-focus-active {\n\t\t\t\t\topacity: 1;\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t}\n\n\t@include media-breakpoint-down(sm) {\n\t\t.navbar-nav .nav-link {\n\t\t\ttext-transform: uppercase;\n\t\t\tfont-size: 80%;\n\t\t\tfont-weight: 500;\n\t\t}\n\t}\n\n\n// #intro\n#intro {\n\t// min-height: 100vh;\n\t// ^ for normal browsers but ie...\n\tmin-height: 37.1875rem + $navbar-height + map-get($spacers, 5);\n\theight: 100vh;\n\tposition: relative;\n\n\t@include media-breakpoint-down(xs) {\n\t\tmin-height: 0;\n\t\theight: auto;\n\t}\n}\n\n\n// #get-started\n.get-started-env {\n\tmax-width: 63em;\n\twidth: 100%;\n\tletter-spacing: normal;\n\tborder-radius: .25em;\n\tbox-shadow: 0 12px 30px rgba(0,0,0,.35);\n\tmargin: 8.6em auto 3.1em;\n\t@include media-breakpoint-down(md) {\n\t\tmargin: 7em auto 2.688em;\n\t}\n\n\t.env-wndw-top {\n\t\theight: 1.55555em;\n\t\tborder-radius: 0.27777em 0.27777em 0 0;\n\t\tbackground-color: #D6D6D6;\n\t\tpadding: 0.472em 0.5em;\n\t\tposition: relative;\n\t\tz-index: 2;\n\n\t\t.btns {\n\t\t\theight: .555556em;\n\t\t\twidth:  .555556em;\n\t\t\tborder-radius: 50rem;\n\t\t\tmargin-right: .45555em;\n\t\t\tfloat: left;\n\n\t\t\t&.red {\n\t\t\t\tbackground-color: #c0392b;\n\t\t\t}\n\t\t\t&.yellow {\n\t\t\t\tbackground-color: #efa42c;\n\t\t\t}\n\t\t\t&.green{\n\t\t\t\tbackground-color: #28ae61;\n\t\t\t}\n\t\t}\n\t}\n\n\t.step-counter {\n\t\theight: 2.333333em;\n\t\twidth: 2.333333em;\n\t\tdisplay: inline-block;\n\n\t\tposition: absolute;\n\t\ttop: -6.2em;\n\t\tleft: 0;\n\n\t\tborder: 1px solid $tab-step-counter-inactive;\n\t\tborder-radius: 50rem;\n\t\tline-height: 2.333333em;\n\n\t\tcolor: $tab-step-counter-inactive;\n\t\tcursor: pointer;\n\n\t\tfont-size: 18px;\n\n\t\t&:before {\n\t\t\tcontent: \"\";\n\t\t\tdisplay: block;\n\t\t\theight: 2.5em;\n\t\t\twidth: 1px;\n\t\t\tposition: absolute;\n\t\t\tborder-right: 1px solid $tab-step-counter-inactive;\n\t\t\tleft: 50%;\n\t\t\ttop: 100%;\n\t\t\tz-index: 0;\n\t\t}\n\t}\n}\n\n// #features\n#features .txt-grid {\n\t@extend .mb-md-2_3;\n\t@extend .mb-lg-0;\n\n\t> [class*='col-'] {\n\t\t// 0, xs, sm\n\t\t@extend .mb-0;\n\t\t&:last-child {\n\t\t\t@extend .mb-2_2;\n\t\t}\n\t\t// md, lg, xl\n\t\t@extend .mb-md-2_3;\n\t\t&:last-child {\n\t\t\t@extend .mb-md-2_3;\n\t\t}\n\t\t// lg, xl\n\t\t@extend .mb-lg-h3_bottom;\n\t\t&:last-child {\n\t\t\t@extend .mb-lg-h3_bottom;\n\t\t}\n\n\t\tp {\n\t\t\t@include media-breakpoint-only(md) {\n\t\t\t\tmargin-bottom: ($spacer / 2);\n\t\t\t}\n\t\t}\n\t}\n}\n\n\n// #foooter\n#footer {\n\t@extend .small;\n\n\ta {\n\t\tfont-size: $font-size-base;\n\t    width: 1em;\n\t    height: 1em;\n\t    line-height: 1em;\n\t    position: relative;\n\t    vertical-align: middle;\n\t    display: inline-block;\n\n\t\t@include hover-focus-active {\n\t\t\ttext-decoration: none;\n\n\t\t}\n\t}\n\n\t#social-icons {\n\t\tli:not(:last-child) {\n\t\t\tmargin-right: 1rem;\n\t\t}\n\n\t\ta {\n\t\t\tcolor: $gray-750;\n\t\t\topacity: .48;\n\n\t\t\t@include hover-focus-active {\n\t\t\t\tcolor: $gray-750;\n\t\t\t\topacity: .65;\n\t\t\t}\n\t\t}\n\t}\n}","// stylelint-disable declaration-no-important, selector-no-qualifying-type, property-no-vendor-prefix\n\n// Reboot\n//\n// Normalization of HTML elements, manually forked from Normalize.css to remove\n// styles targeting irrelevant browsers while